"use client";

import { useState, useEffect } from 'react'
import { blogApi, BlogPost } from '@/lib/supabase'
import Link from 'next/link'
import './feed.css'

export default function FeedPage() {
  const [posts, setPosts] = useState<BlogPost[]>([])
  const [loading, setLoading] = useState(true)
  const [newPost, setNewPost] = useState({ title: '', content: '', author: '' })

  // –ó–∞–≥—Ä—É–∂–∞–µ–º –ø–æ—Å—Ç—ã –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  useEffect(() => {
    loadPosts()
  }, [])

  const loadPosts = async () => {
    setLoading(true)
    const fetchedPosts = await blogApi.getPosts()
    setPosts(fetchedPosts)
    setLoading(false)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (newPost.title.trim() && newPost.content.trim()) {
      const post = await blogApi.createPost(
        newPost.title,
        newPost.content,
        newPost.author || '–ê–Ω–æ–Ω–∏–º'
      )
      
      if (post) {
        setPosts([post, ...posts]) // –î–æ–±–∞–≤–ª—è–µ–º –Ω–æ–≤—ã–π –ø–æ—Å—Ç –≤ –Ω–∞—á–∞–ª–æ
        setNewPost({ title: '', content: '', author: '' })
      }
    }
  }

  const handleLike = async (postId: string) => {
    const updatedPost = await blogApi.likePost(postId)
    if (updatedPost) {
      setPosts(posts.map(post => 
        post.id === postId 
          ? { ...post, likes_count: updatedPost.likes_count }
          : post
      ))
    }
  }

  return (
    <>
      <header className="header">
        <h1>üì∞ –õ–µ–Ω—Ç–∞ –ø–æ—Å—Ç–æ–≤</h1>
        <Link href="/" className="back-button">
          ‚Üê –ù–∞–∑–∞–¥
        </Link>
      </header>

      <main className="feed-container">
        {/* –§–æ—Ä–º–∞ —Å–æ–∑–¥–∞–Ω–∏—è –ø–æ—Å—Ç–∞ */}
        <div className="create-post-section">
          <h2>üìù –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—ã–π –ø–æ—Å—Ç</h2>
          <form onSubmit={handleSubmit} className="post-form">
            <input
              type="text"
              placeholder="–ó–∞–≥–æ–ª–æ–≤–æ–∫ –ø–æ—Å—Ç–∞"
              value={newPost.title}
              onChange={(e) => setNewPost({...newPost, title: e.target.value})}
              className="post-input"
              required
            />
            <input
              type="text"
              placeholder="–í–∞—à–µ –∏–º—è (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)"
              value={newPost.author}
              onChange={(e) => setNewPost({...newPost, author: e.target.value})}
              className="post-input"
            />
            <textarea
              placeholder="–ß—Ç–æ —É –≤–∞—Å –Ω–æ–≤–æ–≥–æ?"
              value={newPost.content}
              onChange={(e) => setNewPost({...newPost, content: e.target.value})}
              className="post-textarea"
              rows={4}
              required
            />
            <button type="submit" className="submit-button">
              –û–ø—É–±–ª–∏–∫–æ–≤–∞—Ç—å
            </button>
          </form>
        </div>

        {/* –°–ø–∏—Å–æ–∫ –ø–æ—Å—Ç–æ–≤ */}
        <div className="posts-section">
          <h2>üìã –í—Å–µ –ø–æ—Å—Ç—ã ({posts.length})</h2>
          
          {loading ? (
            <div className="loading">
              <div className="spinner"></div>
              –ó–∞–≥—Ä—É–∑–∫–∞ –ø–æ—Å—Ç–æ–≤...
            </div>
          ) : posts.length === 0 ? (
            <div className="empty-state">
              <p>üò¥ –ü–æ–∫–∞ –Ω–µ—Ç –ø–æ—Å—Ç–æ–≤. –ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º!</p>
            </div>
          ) : (
            <div className="posts-list">
              {posts.map(post => (
                <div key={post.id} className="post-card">
                  <div className="post-header">
                    <h3 className="post-title">{post.title}</h3>
                    <button 
                      onClick={() => handleLike(post.id)}
                      className="like-button"
                    >
                      ‚ù§Ô∏è {post.likes_count}
                    </button>
                  </div>
                  
                  <div className="post-meta">
                    <span className="post-author">üë§ {post.author_name}</span>
                    <span className="post-date">
                      üìÖ {new Date(post.created_at).toLocaleDateString('ru-RU')}
                    </span>
                  </div>
                  
                  <div className="post-content">
                    {post.content}
                  </div>
                  
                  {post.content.length > 200 && (
                    <button className="read-more">
                      –ß–∏—Ç–∞—Ç—å –¥–∞–ª–µ–µ...
                    </button>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </main>

      <footer className="footer">
        –õ–µ–Ω—Ç–∞ –æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è –≤ —Ä–µ–∞–ª—å–Ω–æ–º –≤—Ä–µ–º–µ–Ω–∏ ‚Ä¢ –í—Å–µ–≥–æ –ø–æ—Å—Ç–æ–≤: {posts.length}
      </footer>
    </>
  )
}